@tailwind base;
@tailwind components;
@tailwind utilities;

@font-face {
  font-family: "Montserrat";
  src: local("Montserrat"),
    url(./fonts/Montserrat/static/Montserrat-Regular.ttf) format("truetype");
}

html, body {
  font-family: "Montserrat";
  overflow-x: hidden;
  padding: 0;
  margin: 0;
}

@keyframes move-left {
  0% {
    transform: translateX(0);
    opacity: 0.5;
  }
  100% {
    transform: translateX(6rem);
    opacity: 1;
  }
}

@keyframes move-right {
  0% {
    transform: translateX(0);
    opacity: 0;
  }
  100% {
    transform: translateX(-6rem);
    opacity: 1;
  }
}

.animate-move-left {
  animation: move-left 0.3s ease-in-out;
}

.animate-move-right {
  animation: move-right 0.3s ease-in-out;
}


/* Add these styles to your CSS file */

.animate-enter-active {
  opacity: 1;
  transform: scale(1.1); /* Slightly larger scale for entry */
  transition: opacity 0.3s ease-in-out, transform 0.3s ease-in-out; /* Smooth transition with ease-in-out */
}

.animate-exit-active {
  opacity: 0.75;
  transform: scale(0.8); /* Slightly smaller scale for exit */
  transition: opacity 0.3s ease-in-out, transform 0.3s ease-in-out; /* Smooth transition with ease-in-out */
}
