.background {
  background: linear-gradient(175deg, #000000, #04364a 70%, #ffffff 30%);
}

.star {
  position: absolute;
  background-image: linear-gradient(
      175deg,
      transparent 70%,
      #ffffff 70%,
      #ffffff
    ),
    url("https://www.transparenttextures.com/patterns/stardust.png");
}

.container {
  @apply xs:max-w-[95%] md:max-w-[75%] mx-auto box-border;
}
